import { atom } from 'recoil';

export interface DesktopIcon {
    name: string;
    type: string;
}

export const desktopIconStore = atom<Array<DesktopIcon>>({
    key: 'desktopIconStore',
    default: [
        { name: ' ', type: 'folder' },
        { name: '  2', type: 'folder' },
        { name: '  3', type: 'folder' },
    ],
});

export const activeIconStore = atom<DesktopIcon | null>({
    key: 'activeIconStore',
    default: null,
});
